@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 60vh;
}

.home h1 {
    font-size: 3rem;
}

.home h2 {
    font-size: 2rem;
}

.home h3 {
    font-size: 1.5rem;
}
.list-movies {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    flex-direction: column;
    width: 60%;
    margin-left: 100px;
    
}

.list-movies li {
    font-size: 1.6rem;
    width: 100%;
}

.list-movies>div {
    list-style: none;
    /* text-align: center; */
}

/* img {
    height: 90px;
    width: auto;
} */

.container-movies{
    display: flex;
    /* flex-wrap: wrap; */
    width: 80%;
    padding-top: 50px;
}

form {
    display: flex;
    justify-content: center;
    padding-top: 50px;
}

.radio, button {
    margin-left: 10px;
}

button {
    width: 5rem;
}

form>input{
    width: 300px;
    font-size: 1.3rem;
}


.container-moviesimdb {
    display: flex;
    /* padding-top: 50px; */
    width: 70%;

    
}

.container-moviesimdb img {
    flex-basis: 50%
}

.container-moviesimdb h2 {
    font-size: 2rem;
    margin-left: 60px;   
}

.container-moviesimdb p {
    margin-left: 60px;
    font-size: 1.3rem;
}

.container-info {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
}
*{
    margin: 0;
    padding: 0;
}

body {
    
    font-family: 'Montserrat', sans-serif;
    background-color: rgba(36, 87, 82, 0.3);
    
}

header {
    height: 20vh;
    background-color: rgba(78, 9, 78, 0.6);
    color: white;
    
    
}

.header-ul {
    display: flex;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    
}

header li {
    list-style: none;
    font-size: 50px;
    color: white;
      
}

.navlink:link {
    color: white;
}

.navlink:active {
    color: blue
}

.navlink:hover {
    color: lightblue
}

.navlink:visited {
    color: white;
}

